.App {
  text-align: center;
  width: 100%;
  
}
h2{
    font-size: 24px;
    text-align: center;
  }
  h3{
    font-size: 18px;
  }
  img {
    border-radius: 5px 5px 0 0;
    padding: 20px;
  }
  .Charactercard{
    background-color: white;
    display: grid;
    grid-template-columns: 300px 300px 300px;
    grid-gap : 100px;
    justify-content: center;
  }
  .cardContainer {
    padding: 2px 16px;
    width: 400px;
    }
  .card{
    align-items: center;
  }
  .img{
    padding: 0 20px 20px 0;
    position: absolute;
  }
  .headbox{
    background-color:white;
    box-sizing: border-box;
    width: 80%;
    margin: 0 auto;
    position:fixed;
    top:0;
    width:100%;
    z-index:100;
  }
  /* .form{
    margin: 0 auto;
    width :80%;
    
  } */
  .search{
    height :35px;
    width : 800px;
    margin-right: 10px;
  }
  .searchbutton{
     background-color: white;
     width :80px;
     margin-top: 30px;
     margin-right: 10px;
  }
  .resetbutton{
    background-color: white;
    width :80px;
    margin-left: 10px;
 }

@media(max-width : 1024px)
{
  .App{
    text-align: center;
    width: 100%;
  }
  .search{
    height :35px;
    width : 75%;
    margin-right: 10px;
  }
  /* .form{
    margin: 0 auto;
    width :80%;
    
  } */
  .Charactercard{
    background-color: white;
    display: grid;
    grid-template-columns: 500px 500px;
    grid-gap : 20px;
    justify-content: center;
    margin: 3px;
  }
}
@media (max-width : 767px)
{
  .App{
    text-align: center;
    width: 100%;
  }
  .search{
    height :35px;
    width : 90%;
    margin-right: 10px;
  }
  /* .form{
    margin: 0 auto;
    width :60%;
    
  } */
  .Charactercard{
    justify-items: center;
    background-color: white;
    display: grid;
    grid-template-columns: 100px;
    }
}

